import React, { useRef, useState } from 'react'
import { Avatar, Box, Card, CardContent, Paper, Tooltip, Typography } from '@mui/material';
import { AspectRatio } from '@mui/icons-material';
import { LoadingButton } from '@mui/lab';
interface ProfileImagenProp {
    perfil: string
}
export const ProfileImagen = ({ perfil }: ProfileImagenProp) => {
    const [loading, setLoading] = React.useState(false);
    const [file, setFile] = useState<any>();
    const inputFileElement = React.useRef<HTMLInputElement>(null);
    const handleChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        if (!e.target.files) {
            return;
        }

        // handle the input...
        console.log(e.target.files);
    }
    const handleClick = () => {
        inputFileElement.current?.click();
    }
    return (
        <Card style={{ width: '100%' }}>
            <CardContent>
                <Typography alignContent={"center"} >Imagen de perfil</Typography>
                <br />
                <Tooltip title="Click para carga nuevo perfil" arrow>
                    <Box
                        display="flex"
                        justifyContent="center"
                        alignItems="center"
                    >
                        <div onClick={handleClick}>
                            <Avatar
                                alt="Imagen perfil"
                                src={`${process.env.REACT_APP_API_COMISIONES}${perfil}`}
                                sx={{ height: '250px', width: '250px' }}
                                style={{ cursor: 'pointer' }}

                            />
                        </div>
                    </Box>
                </Tooltip>
                <br />
                <br />
                <input type='file' ref={inputFileElement} style={{ display: 'none' }} />
                <div style={{ textAlign: 'center' }}>
                    <LoadingButton
                        size="small"
                        onClick={handleClick}
                        loading={loading}
                        loadingIndicator="Loading…"
                        variant="outlined"
                    >
                        <span>Cambiar Foto</span>
                    </LoadingButton>
                </div>
            </CardContent>
        </Card>
    )
}
