import axios from 'axios';
import { AxiosRequest,AxiosResponse } from '../../Utils/Axios';
import { IResponsePerfil } from './Interfaces/IPerfil';

//llamada a interceptor
AxiosRequest();
AxiosResponse();
export async function PerfilService() {
    return await axios.get<IResponsePerfil>(`${process.env.REACT_APP_API_COMISIONES}api/perfil`);
}
