import { Grid, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { PerfilService } from "../../Services/Comisiones/Perfil";
import { ProfileImagen } from "./Components/ProfileImagen";
import { ProfileInformacion } from "./Components/ProfileInformacion";
import { IPerfil } from "../../Services/Comisiones/Interfaces/IPerfil";
import { CBreadcrumbs } from "../../Components/CBreadcrumbs";
import BadgeIcon from '@mui/icons-material/Badge';

const InitialState: IPerfil = {
    ci: "",
    nombreCompleto: "",
    telefono: "",
    fechaNacimiento: "",
    email: "",
    dirrecion: "",
    perfil: ""
}

const Perfil = () => {
    const [{ ci, dirrecion, email, fechaNacimiento, nombreCompleto, perfil, telefono }, SetInformacionPersonal] = useState(InitialState);

    const PerfilApi = async () => {
        try {
            const { data } = await PerfilService()
            SetInformacionPersonal(data.data);
        } catch (error) {

        }
    }
    useEffect(() => {
        PerfilApi();

        return () => {

        }
    }, []);

    return (
        <>
            <CBreadcrumbs icon={<BadgeIcon />} nombreRoute={"Mi perfil"} route={"/perfil"} nombresRoutes={[]} routes={[]}></CBreadcrumbs>
            <Typography variant="subtitle1" component="h6" style={{ fontWeight: 'bold' }}>
                Perfil
            </Typography>
            <Grid container spacing={2}>
                <Grid container item sm={4} sx={{ p: 1 }}  >
                    <Grid container item lg={12} style={{ marginBottom: 25 }} >
                        <ProfileImagen perfil={perfil} />
                    </Grid>
                </Grid>
                <Grid container item sm={8} sx={{ p: 1 }}  >
                    <Grid container item lg={12} style={{ marginBottom: 25 }} >
                        <ProfileInformacion
                            Telefono={telefono}
                            ci={ci}
                            dirrecion={dirrecion}
                            email={email}
                            fechaNacimiento={fechaNacimiento}
                            nombreCompleto={nombreCompleto}
                        />
                    </Grid>
                </Grid>
            </Grid>
        </>
    );
}

export default Perfil;