import React from 'react'
import { Card, CardContent, Grid, Paper, TextField, Typography } from '@mui/material';
import { DatePicker, LocalizationProvider } from '@mui/lab';
interface ProfileInformacionProp {
    ci: string;
    nombreCompleto: string;
    Telefono: string;
    fechaNacimiento: string;
    email: string;
    dirrecion: string;
}
export const ProfileInformacion = ({ Telefono, ci, dirrecion, email, fechaNacimiento, nombreCompleto }: ProfileInformacionProp) => {
    return (

        <Card style={{ width: '100%' }}>
            <CardContent>
                <Typography>Informacion</Typography>
                <br />
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={3}>
                        <TextField
                            required
                            id="ci"
                            name="ci"
                            label="CI"
                            fullWidth
                            autoComplete="given-name"
                            variant="standard"
                            value={ci}
                            disabled={true}
                        />
                    </Grid>
                    <Grid item xs={12} sm={9}>
                        <TextField
                            required
                            id="nombreCompleto"
                            name="nombreCompleto"
                            label="Nombre Completo"
                            fullWidth
                            autoComplete="given-name"
                            variant="standard"
                            value={nombreCompleto}
                            disabled={true}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            required
                            id="fechaNacimiento"
                            name="fechaNacimiento"
                            label="Fecha Nacimiento"
                            fullWidth
                            autoComplete="given-name"
                            variant="standard"
                            value={fechaNacimiento}
                            disabled={true}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            required
                            id="telefono"
                            name="telefono"
                            label="Telefono"
                            fullWidth
                            autoComplete="given-name"
                            variant="standard"
                            value={Telefono}
                            disabled={true}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12}>
                        <TextField
                            required
                            id="email"
                            name="email"
                            label="Email"
                            fullWidth
                            autoComplete="given-name"
                            variant="standard"
                            value={email}
                            disabled={true}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12}>
                        <TextField
                            required
                            id="dirrecion"
                            name="dirrecion"
                            label="Dirrecion"
                            fullWidth
                            autoComplete="given-name"
                            variant="standard"
                            value={dirrecion}
                            disabled={true}
                        />
                    </Grid>
                </Grid>
            </CardContent>
        </Card>

    )
}
